.movies {
  padding-top: 20px;
  padding-bottom: 40px;
  // padding-bottom: 145px; // to fix

  @media screen and (min-width: $tablet) {
    padding-top: 59px;
    padding-bottom: 60px;
    // padding-bottom: 105px; // to fix
  }

  @media screen and (min-width: $desktop) {
    // padding-bottom: 85px; // to fix
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: $indent;
    @media screen and (min-width: $tablet) {
      $indent: 32px;
      gap: $indent;
    }
    @media screen and (min-width: $desktop) {
      $indent: 16px;
      gap: $indent;
    }
  }

  &__card {
    cursor: pointer;
    @media screen and (min-width: $tablet) {
      $indent: 32px;
      $items: 2;
      width: calc((100% - ($indent) * (($items) - 1)) / ($items));
    }

    @media screen and (min-width: $desktop) {
      $indent: 16px;
      $items: 3;
      width: calc((100% - ($indent) * (($items) - 1)) / ($items));
    }
  }

  &__card-photo {
    margin-bottom: 10px;
    width: 280px;
    height: 402px;

    border-radius: 5px;
    transition: box-shadow $cubic;

    @media screen and (min-width: $tablet) {
      margin-bottom: 8px;
      width: 336px;
      height: 455px;
    }
    @media screen and (min-width: $desktop) {
      margin-bottom: 12px;
      width: 395px;
      height: 574px;
    }
    .movies__card:hover & {
      box-shadow: 0px 0px 21px 3px $accent-text-cl;
    }
  }
  &__card-title {
    margin-bottom: 4px;
    font-size: 12px;
    line-height: 1.17;
    color: $black-text-cl;
    @media screen and (min-width: $tablet) {
      line-height: 1.33;
    }
    @media screen and (min-width: $desktop) {
      font-size: 20px;
      line-height: 1.2;
    }
  }
  &__card-text {
    display: flex;
    gap: 5px;
  }
  &__card-genres {
    font-size: 12px;
    line-height: 1.17;
    color: $accent-text-cl;
    @media screen and (min-width: $tablet) {
      line-height: 1.33;
    }
    @media screen and (min-width: $desktop) {
      font-size: 20px;
      line-height: 1.2;
    }
  }
  &__card-rating {
    width: 36px;
    height: 16px;
    padding: 1px 10px;
    text-align: center;
    font-size: 12px;
    line-height: 1.17;
    background-color: $accent-text-cl;
    color: $white-text-cl;
    border-radius: 5px;
    @media screen and (min-width: $desktop) {
      align-self: flex-end;
      width: 40px;
      height: 20px;
      padding: 3px 12px;
    }
  }
}

.movies--padding {
  padding-bottom: 200px; // to fix

  @media screen and (min-width: $tablet) {
    padding-bottom: 150px; // to fix
  }

  @media screen and (min-width: $desktop) {
    padding-bottom: 135px; // to fix
  }
}
